:global(.coin) {
  position: relative;
  perspective: 500px;
  margin: 21px auto 0;
  background: none; }

.coin {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform .4s;
  backface-visibility: hidden !important; }
  .coin :global(div) {
    margin: 0;
    display: block;
    position: absolute;
    width: 100%;
    height: 100%; }
  .coin :global(.front), .coin :global(.back) {
    background: #4C6DA1;
    border-radius: 50%; }
  .coin :global(.front) {
    transform: rotateY(0deg);
    backface-visibility: hidden !important; }
  .coin :global(.back) {
    transform: rotateY(180deg);
    backface-visibility: hidden !important; }
  .coin :global(img) {
    position: absolute;
    height: 100%; }

.coin:global(.flipped) {
  transform: rotateY(180deg); }



